<template>
  <header id="header" class="mdl-layout__header" v-mdl>
    <div class="mdl-layout__header-row">
      <span class="mdl-layout-title">
        <span>
          {{ title }}
        </span>
      </span>
      <div class="mdl-layout-spacer"></div>
      <nav class="mdl-navigation">
        <template v-for="route in topLevelRoutes">
          <router-link class="mdl-navigation__link" :to="routerLinkPath(route)" exact>{{route.meta.headerGroupName}}</router-link>
        </template>
      </nav>
    </div>
  </header>
</template>

<script>
import { mapGetters } from 'vuex';
import lodash from 'lodash';

export default {
  components: {
  },
  data() {
    return {

    };
  },
  computed: {
    ...mapGetters([
    ]),
    title() {
      return this.$route.meta.title || '';
    },
    topLevelRoutes() {
      const routes = this.$router.options.routes;
      const filteredRoutes = lodash.filter(routes, (routeItem) => {
        if (!routeItem.meta) {
          return false;
        }
        return routeItem.meta.headerGroupName;
      });
      return filteredRoutes;
    },
  },
  methods: {
    routerLinkPath(route) {
      return {
        name: route.name,
      };
    },
  },
};
</script>

<style scoped lang="scss">
@import './../../assets/globals.scss';

.router-link-active {
  background-color: $psy-primary-hex;
}
</style>



