import Vue from 'vue';
import VueRouter from 'vue-router';

import HomeView from './../views/HomeView';
import FormComponentsView from './../views/FormComponentsView';
import NotFoundView from './../views/NotFoundView';

const Dummy = { template: '<div>Dummy</div>' };
const router = new VueRouter({
  mode: 'history',
  routes: [
    { path: '/',
      name: 'home',
      component: HomeView,
      meta: {
        title: 'psycokenisis.github.io',
        headerGroupName: 'Home',
      },
    },
    { path: '/components',
      name: 'components',
      component: FormComponentsView,
      meta: {
        title: 'Components',
        headerGroupName: 'Components',
      },
    },
    { path: 'dummy', component: Dummy },
    { path: '*', component: NotFoundView },
  ],
});

Vue.use(VueRouter);
export default router;



